* {
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(to right, #c4c6cf, #afb1b8, #acadb1);
  font-family: Ubuntu, sans-serif;
}

.form {
  max-width: 700px;
  width: 80%;
  opacity: .9;
  border-radius: 10px;
  flex-direction: column;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  box-shadow: 0 54px 55px rgba(66, 65, 65, .25), 0 -12px 30px rgba(49, 48, 45, .68), 0 4px 6px rgba(20, 20, 20, .12), 0 12px 21px rgba(22, 21, 21, .17), 0 -3px 5px rgba(17, 17, 17, .09);
}

.label__container {
  justify-content: space-evenly;
  margin-top: 60px;
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
}

.label {
  width: 100%;
  display: flex;
}

.input {
  width: 100%;
  height: 40px;
  text-align: center;
  color: #000;
  background: linear-gradient(to right, #b0b2b4, #cecfd1, #f0f5f7);
  border-radius: 5px;
  font-family: Ubuntu, sans-serif;
  font-size: 26px;
  font-weight: 500;
  transition: all .4s;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.input:hover {
  outline: .5px solid #000;
  transition: all .4s;
  box-shadow: inset 0 30px 60px -12px rgba(50, 50, 93, .25), inset 0 18px 36px -18px rgba(0, 0, 0, .3);
}

.input::-ms-input-placeholder {
  color: #000;
}

.input::placeholder {
  color: #000;
}

.input:focus {
  outline: none;
}

.select {
  width: 50%;
  height: 44px;
  color: #000;
  cursor: pointer;
  background: linear-gradient(to right, #b0b2b4, #cecfd1, #f0f5f7);
  border-radius: 5px;
  outline: .5px solid #000;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10%;
  font-family: Ubuntu, sans-serif;
  font-size: 1.5em;
  font-weight: 700;
  transition: all .4s;
}

.select:hover {
  outline: .5px solid #000;
  transition: all .4s;
  box-shadow: inset 0 30px 60px -12px rgba(50, 50, 93, .25), inset 0 18px 36px -18px rgba(0, 0, 0, .3);
}

.select:focus {
  outline: none;
}

.option {
  color: #000;
  background: rgba(0, 0, 0, .2);
  font-size: 1em;
  font-weight: 800;
}

.button {
  height: 50px;
  text-align: center;
  width: 30%;
  color: #000;
  cursor: pointer;
  background: linear-gradient(to right, #b0b2b4, #cecfd1, #f0f5f7);
  border-radius: 5px;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  font-family: Ubuntu, sans-serif;
  font-size: 1.5em;
  font-weight: 500;
  transition: all .4s;
  display: flex;
}

.button:hover {
  outline: .5px solid #000;
  transition: all .4s;
  box-shadow: inset 0 30px 60px -12px rgba(50, 50, 93, .25), inset 0 18px 36px -18px rgba(0, 0, 0, .3);
}

.ethereum__icon {
  height: 20px;
}

.loader {
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin: 200px auto 82px;
  font-size: 10px;
  animation: mulShdSpin 1.1s infinite;
  transform: translateZ(0);
}

.result__container {
  width: 50%;
  height: 70px;
  text-align: center;
  background: linear-gradient(to right, #dde1e6, #c2c5c9, #ced4d6);
  border: 1px solid rgba(0, 0, 0, .5);
  border-radius: 5px;
  margin: 140px auto 80px;
  transition: all 1s;
  box-shadow: inset 0 30px 60px -12px rgba(49, 49, 48, .25), inset 0 18px 36px -18px rgba(241, 241, 239, .3);
}

.result {
  color: #000;
  padding-top: 12px;
  font-size: 2em;
  font-weight: 500;
  transition: all .5s;
}

.usageCounter {
  text-align: center;
  color: #050505;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.5em;
  font-weight: 500;
  transition: all .5s;
}

@media screen and (max-width: 768px) {
  .input {
    font-size: 24px;
  }

  .button {
    width: 50%;
  }

  .result__container {
    width: 70%;
  }
}

@media screen and (max-width: 480px) {
  .form {
    margin-top: 40px;
  }

  .label__container {
    flex-direction: column;
    margin-top: 40px;
  }

  .label {
    width: 50%;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
  }

  .button {
    width: 50%;
    margin-top: 40px;
    font-size: 20px;
  }

  .loader {
    margin-top: 90px;
  }

  .result__container {
    height: 60px;
    margin-top: 80px;
    margin-bottom: 40px;
  }

  .result {
    padding-top: 16px;
    font-size: 24px;
  }

  .usageCounter {
    font-size: 18px;
  }
}

